.container {
    display: flex;
    flex-direction: column;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
    justify-content: space-around;
    border: solid gray;
    margin: 5px;
    width: calc(100% - 76%);
}

.top {
    display: flex;
    justify-content: center;
;
}

.left, .right {
    flex-wrap: wrap;
    text-align: left;
    display: flex;
    flex-direction: column;
    border: solid #794a00;
    margin: 10px;

    flex-basis: 20%;
    justify-content: space-around;
    width: auto;
}

.left {
    width: 400px;
    font-size: 40px;
    margin: 10px;
    display: flex;
    flex-direction: row;
}

.right {
    font-size: 25px;
    flex-direction: initial;
    display: flex;

}

.right-sub {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin: 2px;
}

.middle {
    display: flex;
    flex-wrap: wrap-reverse;
}

.middle-left {
    min-width: auto;
    justify-content: center;
}

.middle-right, .middle-left {
    width: 67%;
    display: flex;
    flex-flow: wrap;
    text-align: center;
}

.logo{
    height: 100px;
}



.card-container ,.card-container-pie{
    margin: 10px;
    padding: 10px;
    font-family: 'Titillium Web', sans-serif;
    display: flex;
    border: solid #00F9FF;
    -webkit-animation: shadowBox 3s infinite;
            animation: shadowBox 3s infinite;
    flex-direction: column;
    justify-content: center;
    width:200px;
    height: 300px;
    border-radius: 15px;
}

.card-container-pie {
    width: calc(100% + 34%);
    height: auto;
}

.card-top {
    margin: 5px;
    background-color: #00f9ff;
    color: black;
    border-bottom: solid #484848;
    border-radius: 9px;

}
@-webkit-keyframes shadowBox {
    0% {box-shadow: 2px 1px 18px #00f9ff;}
    50% {box-shadow: 2px 1px 5px #00999d;}
    100% {box-shadow: 2px 1px 18px #00f9ff;}

}
@keyframes shadowBox {
    0% {box-shadow: 2px 1px 18px #00f9ff;}
    50% {box-shadow: 2px 1px 5px #00999d;}
    100% {box-shadow: 2px 1px 18px #00f9ff;}

}

.card-context {
    font-size: 60px;
    height: auto;
    line-height: 111px;
}

.card-context-pie {
    font-size: 80px;
    width: 100%;

}

.card {
    border-radius: 15%;
}
.App {
    background-color: black;
    color: orange;
    display: flex;

}


